
body {
    -webkit-font-smoothing: antialiased; 
    text-rendering: optimizeLegibility;
    color: #666;
    font-family: "Microsoft YaHei", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
    background-color: #fff;
    line-height: 1.8;
    margin: 0;    
    padding: 0;
    overflow-x: hidden;

}
a {
    background: transparent;
    color: #666;
    text-decoration: none;
    -webkit-transition: color .2s;
    transition: color .2s;
    &:hover{
        color: $color-primary;
    }
}
figure{
    padding: 0;margin:2rem 0;
}
img {
    height: auto;
    max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Microsoft YaHei", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
    font-weight: normal;
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

input,textarea,button{
    font-family: "Microsoft YaHei", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}



.table-responsive{
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
}
.container{
    @include respond-above (xlg) {
        max-width: 1200px;
    }
}
.container-fluid{
    max-width: 1780px;
}

.page {  
  height: 100%;
    display: flex;
    align-items: stretch;
   

    .leftcol{
        width: 22%;
        background-color: #f6f6f6;
     
    }
    .maincontent{
        width: 78%;
        padding-top: 15rem;
        padding-left: 130px ;
        padding-right: 130px;
        @include respond-above(lg) {
            padding-left:180px ;
            padding-right: 180px;  padding-top: 25rem;
        }
        @include respond-above(xl) {
            padding-left: 230px ;
            padding-right: 230px;   padding-top: 35rem;
        }
       
        .wrap{
            max-width: 1080px;        
            margin: 0 auto;
        }
    }
}

.title {
    color: $color-text-heading;
    font-weight: normal;
}

.list {
    margin-bottom: 1rem;
}

.banner {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 1000;

   img{
       width: 100%;
       max-width: none;
   }
   .title-page{
       width: 300px;
       height: 309px;
       background: url('../img/bg_title.png') no-repeat center center;
       background-size: contain;
       position: absolute;
       left: 22%;
       margin-left: -150px;
       bottom: -140px;
       z-index: 100;     
       color: #fff;
       @include respond-above(lg) {
            width: 400px;
            height: 412px;bottom: -200px; margin-left: -238px;
        }
        @include respond-above(xl) {
            width: 477px;
            height: 492px;  bottom: -200px; margin-left: -238px;
        }
        &.title-page-product{
            background-image: url('../img/bg_title_product.png')
        }
        &.title-page-support{
            background-image: url('../img/bg_title_support.png')
        }
        &.title-page-news{
            background-image: url('../img/bg_title_news.png')
        }
        &.title-page-application{
            background-image: url('../img/bg_title_application.png')
        }
        &.title-page-solution{
            background-image: url('../img/bg_title_solution.png')
        }


       .txt{
        @include vertical-align();   padding-left: 3rem;
        margin-top: -0.5rem;
        @include respond-above(lg) {
            padding-left: 4.5rem;   margin-top: -0.8rem;
        }
        @include respond-above(xl) {
            padding-left: 6rem;   margin-top: -1rem;
        }




        h2{
            font-size: rem(40);
            font-family: 'DINCond_Book';
            line-height: 1; margin: 0;
            @include respond-above(lg) {
                font-size: rem(50);
            }
              @include respond-above(xl) {
                font-size: rem(60);
            }
        }
        h3{
            font-size: rem(20);
            margin: 0;
        }
       }
    
   }
   
   
}


.title-section{   
    margin-bottom: 1.5rem;
    h2{
        font-size: rem(24);   margin: 0;  
        text-align: center; 
        @include respond-above(sm) {
            font-size: rem(28);
        }
        @include respond-above(md) {
            font-size: rem(32);
        }
        @include respond-above(lg) {
            font-size: rem(36);
        }
        @include respond-above(xlg) {
            font-size: rem(40);
        }
       
       
       
    }

 
}
.title-section-v1{
    text-align: left;
   border-bottom: 1px #e5eaeb solid;
    
    margin-bottom: 2rem;
    padding-bottom: 1rem;
    h3{
        font-size: rem(18);      
        line-height: 1;      
        text-transform: uppercase;
        border-left: 8px $color-primary solid;padding-left: .6rem;
        margin: 0;
    }
    
}
.title-section-v2{
    text-align: center;   
    margin-bottom: 2rem;
   
    h3{
        margin: 0;line-height: 1;
        color: #333;
        font-size: 1rem;
        display: inline-block;
        font-weight: bold;
        position: relative;
        @include respond-above(sm) {
            font-size: rem(18);
        }
        @include respond-above(md) {
            font-size: rem(20);
        }
        @include respond-above(lg) {
            font-size: rem(22);
        }
        &::before,&::after{
            position: absolute;
            content: " ";
            display: block;
            background-color: $color-primary;
            height:1px;
            width: 50px;
            top: .6rem;
            left: -110px;          
            z-index: 100;
          
        }
        &::after{
            left: auto;
            right: -110px;          
        }
    }
}

.title-node{
    border-left: 8px $color-primary solid;
    padding-left: 1rem;
    font-size: rem(20);
    line-height: 1.2;
}

.title-v1{
    font-size: rem(18);   
    color: #222;
    @include respond-above(lg) {
        font-size: rem(20);
    }
}

.table-container{
    overflow-x: auto;
    
}
.table{
    width: 100%;
    margin:  0;
    border-collapse: collapse;
    th{
     text-align: center;
        color:#333;
        padding:10px 3px;
        white-space: nowrap;
        border-bottom: 2px #e5e5e5 solid;
    }
    td{
        padding: 10px 1rem;       
    }
}  